import React from 'react'
import Dead from '../assets/dead.jpg'
import Demo from '../assets/demo.jpg'
import Entry from '../assets/enter.jpg'

const Showcase = () => (
    <div style={{
        fontFamily: 'Raleway',
        fontSize: '18px',
        color: '#F9CF87',
        textShadow: '1px 1px black',
        height:'100%',
    }}>
        <h1>Screen captures</h1>
       <div className={'pictures'}>
        <img src={Demo} />
        <p>Dev-only level to test out moving platforms and wall jumping</p>
        <img src={Entry} />
        <p>A part of the very first level</p>
        <img src={Dead} />
        <p>Protagonist was killed by an enemy, death is always near</p>
       </div>
    </div>
)

export default Showcase



